.hd-file-upload {
  font-size: 0;
  padding: 0;
  margin: 0;
  position: relative;
}
.hd-file-upload input[type="file"] {
  width: 0;
  height: 0;
}
.hd-file-upload__button {
  width: 100%;
  position: relative;
}
.hd-file-upload__button button {
  width: 100%;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1;
  background-color: #1e88e5;
  margin: 0;
  padding: 0.75rem;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.hd-file-upload__button button i {
  color: #fff;
  margin-right: 0.5rem;
}
.hd-file-upload__button button label {
  color: #fff;
}
.hd-file-upload__button button:hover {
  cursor: pointer;
  background-color: #1976d2;
}
.hd-file-upload__button button:disabled {
  background-color: #aeaeae;
}
.hd-file-upload__button button:disabled i,
.hd-file-upload__button button:disabled label {
  color: #494949;
}
.hd-file-upload__progress {
  position: absolute;
  width: 100%;
  height: 0.25rem;
  left: 0;
  right: 0;
  bottom: 0;
}
.hd-file-upload__progress--inner {
  position: absolute;
  left: 0;
  bottom: 0;
  top: 0;
  background-color: #dd2c00;
}
.hd-file-upload__error {
  position: absolute;
  margin-top: -0.625rem;
  background-color: #dd2c00;
  color: #fff;
  font-size: 0.75rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  margin-left: 0.5rem;
  top: 50%;
  height: 1.25rem;
  border-radius: 0.125rem;
}
